.product-details-wrapper {
    @include flexBox(column,space-between,center);
    width: calc(100% - 109px);
    height: auto;
    margin: 113px 0 -25px 77px;
    
    @include tablet-down {
        width: calc(100% - 15px);
        max-height: 1943px;
        margin: 0;
        margin-top: 34px;
    }
    @include tablet {
        width: calc(100% - 15px);
        max-height: 1943px;
        margin: 0;
        margin-top: 34px;
    }

    .product-details-section1 {
        @include flexBox(row,space-between,center);
        width: 100%;
        min-height: 681px;
        margin-bottom: 49px;

        @include tablet-down {
            @include flexBox(column,'','');
            height: 100%;
        }
        @include tablet {
            @include flexBox(column,'','');
            width: 100%;
            height: 100%;
        }

        .image-box {        
            @include flexBox(row,space-between,center);
            width: 669px;
            height: 100%;
            margin-right: 33px;

            .mobile-slider-comp {
                display: none;

                @include tablet-down {
                    width: 100%;
                    display: block;
                    margin-top: 24px;
                }
                @include tablet {
                    width: 100%;
                    display: block;
                    margin-top: 24px;
                }
            }

            @include tablet-down {
                width: 100%;
                height: 410px;
                margin: 0;
            }
            @include desktop-down {
                width: 100%;
                height: 410px;
                margin: 0;
            }

            .image-slider {
                @include flexBox(column,space-between,'');
                width: 100px;
                height: 680px;
                margin-right: 17px;

                @include tablet-down {
                    @include flexBox(row,center,center);
                    display: none;
                    width: 100%;
                    height: 310px;
                    margin-right: 0;
                }
                @include tablet {
                    @include flexBox(row,center,center);
                    display: none;
                    width: 50%;
                    height: 310px;
                }

                .slider-img {
                    width: 100px;
                    height: 120px;
                    cursor: pointer;

                    &:first-child, &:hover {
                        border-bottom: 4px solid $primary-brand;
                    }

                    &:last-child {
                        width: 100%;
                        height: 20px;
                    }

                    @include tablet-down; @include tablet {
                        &:last-child {
                            display: none;
                        }
                        width: 100%;
                        height: 100%;
                    }
                }
                .btn-arrow-down {
                    @include flexBox(row, center,center);
                    width: 100px;
                    height: 40px;
                    cursor: pointer;

                    &:hover{
                        background-color: $gray-l1;
                    }
                }
            }

            .display-image {
                width: 552px;
                height: 680px;
                img {
                    width: 100%;
                    height: 100%;
                    
                    @include tablet-down {
                        width: 100%;
                        height: 390px;
                        margin-top: 60px;
                        display: none;
                    }
                    @include tablet {
                        width: 100%;
                        height: 390px;
                        margin-top: 60px;
                        display: none;
                    }
                }

                @include tablet-down {
                    display: none;
                }
                @include tablet {
                    display: none;
                }
            }
        }
        .product-attributes {
            @include flexBox(column, '', '');
            width: 439px;
            height: 681px;

            @include tablet-down; @include desktop-down {
                width: calc(100% - 20px);
                height: 680px;
            }

            font: {
                style: $unnamed-font-style-normal;
                variant: normal;
                weight: $unnamed-font-weight-normal;
                size: $unnamed-font-size-14;
                family: $unnamed-font-family-muli;
            }
            line-height: $unnamed-line-spacing-20;
            letter-spacing: $unnamed-character-spacing-0;
            text-align: left;

            @include desktop-down {
                font-size: $unnamed-font-size-20;
            }

            .route-bar {
                margin-bottom: -10px;
                @include tablet-down {
                    position: absolute;
                    height: 20px;
                    width: calc(100% - 20px);
                    margin: -421px 0 0 -19px;
                }
                @include tablet {
                    position: absolute;
                    height: 20px;
                    width: calc(100% - 20px);
                    margin: -427px 0 0px -10px;
                }
                p {
                    height: 18px;
                    margin-bottom: 22px;
                    @include tablet-down {
                        font-size: 16px;
                        width: 100%;
                        line-height: 8px;
                        margin-left: 10px;
                    }
                    @include tablet {
                        font-size: 16px;
                        width: 100%;
                        line-height: 8px;
                        margin-left: 10px;
                    }
                }
            }
            h2 {
                margin-bottom: 13px;
                font: {
                    weight: $unnamed-font-weight-600;
                    size: $unnamed-font-size-28;
                    family: "Source Serif Pro";
                }
                line-height: $unnamed-line-spacing-42;
                @include tablet-down; @include desktop-down {
                    margin-top: 16px;
                    font-size: $unnamed-font-size-24;
                    line-height: $unnamed-line-spacing-30;
                }
            }
            h4 {
                font: {
                    weight: $unnamed-font-weight-600;
                    size: $unnamed-font-size-20;
                }
                line-height: $unnamed-line-spacing-30;
            }
            .product-rating {
                @include flexBox(row,'','');
                height: 18px;
                margin: 16px 0;
                .stars {
                    width: 108px;
                    img {
                        width: 16px;
                        height: 15px;
                        margin-right: 4px;
                    }
                }
                p {
                    width: 35px;
                }
            }
            .product-descp {
                border-bottom: 1px solid $gray-m1;
                height: 64px;
                margin-bottom: 16px;
                p {
                    font-size: $unnamed-font-size-16;
                    line-height: $unnamed-line-spacing-24;
                    width: 100%;
                }
                @include tablet-down {
                    height: 86px;
                }
                @include tablet {
                    height: 86px;
                }
            }
            .quantity-box {
                @include flexBox(row,space-between,center);
                width: 100%;

                @include tablet-down; @include desktop-down {
                    @include flexBox(row,center,center);
                }
                span {
                    @include flexBox(row,space-between,center);
                    width: 202px;
                    height: 40px;
                    margin: {
                        top: 16px;
                        bottom: 40px;
                    }
    
                    button, input {
                        background: $white 0% 0% no-repeat padding-box;
                        border: 2px solid $gray-m1;
                        text-align: center;
                        font: {
                            weight: $unnamed-font-weight-600;
                            size: $unnamed-font-size-16;
                        }
                        color: $unnamed-color-172026;
                    }
                    input {
                        font-size: 16px;
                        color: #91959C;
                        width: 114px;
                        height: 40px;
                    }
                    button {
                        mix-blend-mode: normal;
                        border-radius: 50%;
                        width: 32px;
                        height: 32px;
                        display: inline-flex;
                        align-items: center;
                        text-align: center;
                        font-size: 26px;
                        font-family: normal;
                        color: #91959C;
                        cursor: pointer;

                        &:disabled {
                            background-color: $gray-l1;
                        }
                    }
                    input {
                        width:58%;
                        height: 100%;
                        border-radius: 8px;
                        margin: 0 0;
                    }
                }
            }
            #btn-add-to-cart {
                width: 318px;
                height: 40px;
                background: $primary-brand 0% 0% no-repeat padding-box;
                border: none;
                line-height: 16px;
                color: $white;
                margin-bottom: 24px;
                cursor: pointer;

                @include tablet-down; @include desktop-down {
                    width: calc(100% - 30px);
                    margin-left: 15px;
                }
            }
            .extra-buttons {
                @include flexBox(row,center,center);
                width: 360px;

                @include tablet-down; @include desktop-down {
                    width: 100%;
                    margin-left: 20px;
                }
                button {
                    margin-right: 32px;
                    color: $unnamed-color-172026;
                    background-color: $white;
                    border: none;
                    font: {
                        style: $unnamed-font-style-normal;
                        variant: normal;
                        weight: $unnamed-font-weight-600;
                        size: $unnamed-font-size-16;
                        family: $unnamed-font-family-muli;
                    }
                    line-height: $unnamed-line-spacing-20;
                    letter-spacing: 0.32px;
                    img {
                        margin-right: 8px;
                        width: 18px;
                        height: 16px;
                    }
                }
            }
        }
    }
    .product-details-section2 {
        width: calc(100% - 20px);
        height: auto;

        @include tablet-down; @include desktop-down {
            @include flexBox(column,space-between,'');
            height: 50%;
            align-items: center;
            margin-top: 24px;
        }

        .product-info, a {
            font: {
                style: $unnamed-font-style-normal;
                variant: normal;
                weight: $unnamed-font-weight-600;
                size: $unnamed-font-size-20;
                family: $unnamed-font-family-muli;
            }
            line-height: $unnamed-line-spacing-25;
            letter-spacing: $unnamed-character-spacing-0;
            color: $unnamed-color-172026;
            text-align: left;
            @include tablet-down; @include desktop-down {
                text-align: center;
            }
        }

        .product-info {
            display: block;
            width: calc(100% + 20px);

            @include tablet-down {
                @include flexBox(column,space-between,'');
                width: 100%;
            }
            @include tablet {
                @include flexBox(column,space-between,'');
                width: 100%;
            }

            h2 {
                line-height: $unnamed-line-spacing-45;
            }
            h4 {
                margin-bottom: 16px;
            }

            .product-description {
                margin-right: 34px;

                @include tablet-down {
                    width: 100%;
                    margin-right: 0;
                }
                @include tablet {
                    width: 100%;
                    margin-right: 0;
                }
                h2 {
                    font: {
                        size: $unnamed-font-size-36;
                        family: "Source Serif Pro";
                    }
                    margin-bottom: 24px;
                    @include tablet-down {
                        margin-bottom: 16px;
                        font-size: $unnamed-font-size-24;
                        line-height: $unnamed-line-spacing-30;
                    }
                }

                p {
                    font: {
                        weight: $unnamed-font-weight-normal;
                        size: $unnamed-font-size-16;
                    }
                    line-height: $unnamed-line-spacing-24;
                    @include tablet-down; @include desktop-down {
                        text-align: left;
                    }
                }
            }
        }
        .ending-line {
            width: 102px;
            height: 8px;
            background: $primary-brand 0% 0% no-repeat padding-box;
            margin-top: 24px;
            @include tablet-down; @include desktop-down {
                margin-bottom: 36px;
            }
        }
    }
}