.home {
    h1 {
        font: {
            style: $unnamed-font-style-normal; 
            variant: normal; 
            weight: $unnamed-font-weight-600; 
            size: $unnamed-font-size-48;
            family: $unnamed-font-family-source-serif-pro;
        }
        line-height: $unnamed-line-spacing-60;
        letter-spacing: $unnamed-character-spacing-0;
        color: $unnamed-color-172026;
    }
    
    h2, p, button, link {
        font: {
            style: $unnamed-font-style-normal;
            variant: normal;
            weight: $unnamed-font-weight-normal; 
            size: $unnamed-font-size-16;
            family: $unnamed-font-family-muli;
        }
        letter-spacing: $unnamed-character-spacing-0;
    }
    
    button {
        width: 220px;
        height: 40px;
        color: $white;
        background-color: $primary-brand;
        border: none;
        box-shadow: 0px 3px 6px #39373729;
        cursor: pointer;
    
        font: {
            weight: $unnamed-font-weight-bold;
            size: $unnamed-font-size-14;
        }
        line-height: $unnamed-line-spacing-16;
    
        &:hover {
            transition: all 0.4s ease-in-out;
            background-color: $primary-d1;
        }
    }
    
    .hero-banner-1 {
        @include flexBox(row,space-between,center);
        max-width: 1920px;
        margin: auto;
        background-color: $gray-39373729;
        height: 650px;
        .hero-banner-info {
            @include flexBox(column, flex-start, flex-start);
            margin-left: 20px;
            width: 552px;
            margin: auto;
    
            h1 {
                width: 442px;
                margin-bottom: 8px;
            }
            p {
                line-height: $unnamed-line-spacing-24;
                margin-bottom: 24px;
            }
        }
        .hero-banner-image{
            width: 51%;
            height: 650px;
            background: transparent url('../../assets/images/three-men-clothing.png') 10% 20% no-repeat padding-box;
        }
    }
    
    .category-display-wrapper {
        @include flexBox(row, space-between, center);
        width: 100%;
        height: 320px;
        margin-top: -105px;
        background-color: $white;
        margin-bottom: 59px;
        .category-display-box {
            max-width: 349px;
            width: calc(100% - 5px);
            height: 320px;
            padding: 222px 13px 0 5px;
            cursor: pointer;

            &.box-1 {
                background: transparent url('../../assets/images/women-cloth.PNG') 0% 0% no-repeat padding-box ;
            }
            &.box-2 {
                background: transparent url('../../assets/images/men-cloth.PNG') 0% 0% no-repeat padding-box ;
            }
            &.box-3 {
                background: transparent url('../../assets/images/jwellery.PNG') 0% 0% no-repeat padding-box;
            }
            &.box-4 {
                background: transparent url('../../assets/images/electronics.PNG') 0% 0% no-repeat padding-box;
            }
    
            &.box-1, &.box-2, &.box-3, &.box-4 {
                background-size: contain;
            }
    
            .category-display-info {
                max-width: 331px;
                width: calc(100% + 5px);
                height: 98px;
                background: $grey-1B252CD9 0% 0% no-repeat padding-box;
                border-bottom: 8px solid $primary-brand;
                padding: 17px 0 0 38px;
    
                font: {
                    style: $unnamed-font-style-normal; 
                    variant: normal; 
                    weight: $unnamed-font-weight-600; 
                    family: $unnamed-font-family-source-serif-pro;
                }
                line-height: $unnamed-line-spacing-30;
                letter-spacing: $unnamed-character-spacing-0;
                color: $white;
    
                h2 {
                    font-size: $unnamed-font-size-24;;
                }
                p {
                    font: {
                        weight: $unnamed-font-weight-normal; 
                        size: $unnamed-font-size-16;
                        family: $unnamed-font-family-muli;
                    }
                    line-height: $unnamed-line-spacing-24;
                    letter-spacing: 0.32px;
                }
            }
        }
    }
    
    .hero-banner-2 {
        @include flexBox(row, space-between, center);
        width: calc(100%  - 16px);
        height: 450px;
        margin: auto;
        .hero-banner-2-text {
            margin: auto;
            h1 {
                width: 411px;
                margin-bottom: 8px;
            }
            h2 {
                font: {
                    weight: $unnamed-font-weight-600;
                    size: $unnamed-font-size-20;
                }
                line-height: $unnamed-line-spacing-25;
                margin-bottom: 16px;
            }
            p {
                width: 631px;
                line-height: $unnamed-line-spacing-24;
                margin-bottom: 24px;
            }
            .hero-banner-2-buttons {
                width: 475px;
                @include flexBox(row, space-between, center);
                margin-bottom: 56px;
    
                & > :nth-child(1) {
                    background: $white 0% 0% no-repeat padding-box;
                    border: 2px solid #D4703D;
                    color: $primary-brand;
    
                    &:hover {
                        transition: all 0.4s ease-in-out;
                        border: 2px solid $primary-d1;
                    }
                }
            }
            .line {
                width: 102px;
                height: 8px;
                background: $primary-brand 0% 0% no-repeat padding-box;
            }
        }
        .hero-banner-1-image {
            width: 40%;
            height: 100%;
            background: transparent url('../../assets/images/men-in-shade.png') 0% 12% no-repeat padding-box;
            background-size: cover;
    
        }
    }
    
    .hero-banner-3 {
        @include flexBox(row,center,center);
        height: 432px;
        margin-top: 48px;
    
        .hero-banner-3-image{
            max-width: 836px;
            width: 100%;
            height: 92.6%;
            img {
                width: 100%;
                height: 100%;
            }
        }
        .hero-banner-3-info {
            @include flexBox(column, center, '');
            background: #1B252C 0% 0% no-repeat padding-box;
            max-width: 554px;
            width: 100%;
            height: 100%;
            padding: 0 0 0 10%;
            margin: auto;
            color: $white;
    
            h1 {
                width: 326px;
                margin-bottom: 16px;
                color: $white;
            }
            p {
                font: {
                    weight: $unnamed-font-weight-600;
                    size: $unnamed-font-size-20;
                }
                line-height: $unnamed-line-spacing-25;
                margin-bottom: 36px;
            }
            button {
                width: 180px;
                height: 40px;
                background-color: transparent;
                border: 2px solid $white;
                border-radius: 10px;
                padding: 11px 20px;
            }
        }
    }
}