.container {
    max-width: 1411px;
    margin: auto;
    position: relative;
    width: 100%;
    padding: 0 20px;
    margin-top: 80px;


    @include desktop-down {
      max-width: 992px;
      margin: auto;
      position: relative;
      width: 100%;
      padding: 0 16px;
      margin-top: 56px; 
    }
}

