.header-wrapper {
    top: 0;
    left: 0;
    position: fixed;
    z-index: 999;
    background: $primary-brand 0% 0% no-repeat padding-box;
    width: 100%;
    height: 80px;

    @include desktop-down {
        height: 56px;
        padding: 6px 0px;
    }

    .header {
        @include flexBox(row, space-between, center);
        margin-top: -57.5px;
        height: 35px;

        @include desktop-down {
            @include flexBox(row, space-between, center);
            width: 100%;
            height: 48px;
        }
    
        font: {
            style: $unnamed-font-style-normal;
            variant: normal;
            weight: $unnamed-font-weight-600;
            size: $unnamed-font-size-16;
            family: $unnamed-font-family-muli;
        }
        line-height: $unnamed-line-spacing-24;
        letter-spacing: $unnamed-character-spacing-0;
        color: $white;
        
        #menuButton {
            display: none;

            @include desktop-down {
                display: block;
                padding: 18px 15px;
            }

            img {
                width: 18px;
                height: 12px;
            }
        }
        .logo {
            width: 78px;
            height: 35px;

            @include desktop-down {
                width: 102px;
            }
            img {
                width: 100%;
                height: 100%;
                object-fit:contain;
            }
        }
    
        a {
            text-decoration: none;
        }
    
        .header-menu-1 {
            width: 439px;
            height: 20px;
            ul {
                display: flex;
                justify-content: space-between;
                cursor: pointer;

                li {
                    &.active {
                        height: 50px;
                        border-bottom: 5px solid $primary-l1;
                    }
                    &:hover{
                        height: 50px;
                        border-bottom: 5px solid $primary-l1;
                    }
                }
            }

            @include desktop-down {
                display: none;
            }
        }
    
        .header-menu-2 {
            height: 22px;
            width: 24px;
            margin-right: 8px;
            margin-top: 8px;
            align-items: center;
            .header-menu-2-link {
                font: {
                    style: $unnamed-font-style-normal;
                    variant: normal;
                    weight: $unnamed-font-weight-600;
                    size: $unnamed-font-size-16;
                    family: $unnamed-font-family-muli;
                }
                line-height: $unnamed-line-spacing-24;
                font-size: 16px;
                color: $white;
                img {
                    width: 100%;
                    height: 100%;
                }
                .cart-item-count-display {
                    @include flexBox(row, center,center);
                    color: $white;
                    height: 23px;
                    width: 23px;
                    border-radius: 20px;
                    background-color: $primary-m1;
                    font: {
                        weight: $unnamed-font-weight-600;
                        size: $unnamed-font-size-12;
                        family: $unnamed-font-family-muli;
                    } 
                    line-height: $unnamed-line-spacing-15;
                    margin: -40px 0 0 12px;
                    position: absolute;
                    padding: 3px 5px 5px 4px;

                    @include desktop-down {
                        height: 20px;
                        width: 20px;
                        font-size: 14px;
                        margin: -40px 0 0 11px;
                    }
                    // @include tablet {
                    //     height: 20px;
                    //     width: 20px;
                    //     font-size: 14px;
                    //     margin: -40px 0 0 11px;
                    // }
                }
            }
        }
    }
}